<template>
<div>   <!--    面包屑导航-->
  <el-breadcrumb style="padding-bottom:15px " separator="/">
    <el-breadcrumb-item :to="{ path: '/main' }">首页</el-breadcrumb-item>
    <el-breadcrumb-item>树洞小纸条管理</el-breadcrumb-item>
    <el-breadcrumb-item>未审核树洞小纸条管理</el-breadcrumb-item>
  </el-breadcrumb>
  <el-card>
    <!--    搜索头部-->
    <el-row :gutter="40" style="padding-bottom: 15px">
      <el-col :span="8">
        <el-input placeholder="请输入内容" v-model="input3" class="input-with-select" @keyup.enter.native="Search"
                  clearable>
          <el-button slot="append" icon="el-icon-search" @click="Search"></el-button>
        </el-input>
      </el-col>
      <el-col :span="4">
        <el-button type="primary" size="mini" @click="addNote">添加树洞小纸条日记</el-button>
      </el-col>
      <el-col :span="4" style="  position:absolute; left:90%;">
        <el-button type="primary" icon="el-icon-refresh-right" @click="getNoteList"></el-button>
      </el-col>
    </el-row>
    <el-table
        :data="NoteList.data"
        border
        style="width: 100%">
      <el-table-column
          prop="title"
          label="标题"
          width="437">
      </el-table-column>
      <el-table-column
          prop="statecode"
          label="状态"
          width="300">
      </el-table-column>
      <el-table-column
          prop="id"
          label="操作"
          width="500">
        <div slot-scope="a">
          <el-button type="primary" size="mini" @click="updateNote(a.row)">查看/审核</el-button>
          <el-button type="danger" size="mini" @click="deleteNote(a.row)">删除</el-button>
        </div>
      </el-table-column>
    </el-table>
  </el-card>
  <!--      分页控件-->
  <el-pagination
      @size-change="SizeChange"
      @current-change="CurrentChange"
      :current-page="page"
      :page-sizes="[10, 20, 50, 100]"
      :page-size="limit"
      layout="total, sizes, prev, pager, next, jumper"
      :total="NoteList.count">
  </el-pagination>
  <el-dialog
      title="编辑树洞小纸条"
      :visible.sync="dialogUpdateNote"
      width="30%">
    <!--        内容区域-->
    <el-form :model="UpdateNoteList"  ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="标题" prop="title">
        <el-input  type="textarea" :rows="2" v-model="UpdateNoteList.title" ></el-input>
      </el-form-item>
      <el-form-item label="状态" prop="statecode">
        <el-select v-model="UpdateNoteList.statecode" placeholder="请选择">
          <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
    <el-button @click="dialogUpdateNote = false">取 消</el-button>
    <el-button type="primary" @click="updateNote1">保 存</el-button>
  </span>
  </el-dialog>
  <el-dialog
      title="添加树洞小纸条"
      :visible.sync="dialogAddNote"
      width="30%">
    <!--        内容区域-->
    <el-form :model="AddNoteList"  ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="标题" prop="title">
        <el-input  type="textarea" :rows="2" v-model="AddNoteList.title" ></el-input>
      </el-form-item>
      <el-form-item label="状态" prop="statecode">
        <el-input   :rows="2" v-model="AddNoteList.statecode" ></el-input>
      </el-form-item>
      <el-button type="primary" @click="addNote1" class="a">添 加</el-button>
    </el-form>

  </el-dialog>
</div>
</template>

<script>
export default {
  name: "content",
  data(){
    return{
      options:[{
        value:'0',
        label:'审核未通过'
      },
        {
          value: '1',
          label: '审核通过'
        }
      ],
      value:'',
      input3:'',
      page: 1,
      limit: 10,
      statecode:'0',
      NoteList:[],
      UpdateNoteList:{
        id:'',
        title:'',
        statecode:''
      },
      AddNoteList:{
        title: "",
        statecode:''
      },
      dialogAddNote: false,
      dialogUpdateNote: false,
    }
  },
  methods:{
    //添加
    addNote(){
      this.dialogAddNote = !this.dialogAddNote
    },
    addNote1(){
      this.dialogAddNote = !this.dialogAddNote,
          this.$http.get("/NoteController/addNote", {
            params: {
              id: this.AddNoteList.id,
              title: this.AddNoteList.title,
              statecode: this.AddNoteList.statecode
            }
          }).then((res) => {
            console.log(res)
            this.NoteList = res.data;
            if (res.data.code === 1) {
              this.$message.success("添加成功")
              this.getNoteList();
            } else this.$message.error("添加失败")
          })
    },
    //请求查看用户列表数据
    getNoteList() {
      this.$http.get("/NoteController/selectnoVerifyNote", {
        params: {
         statecode:this.statecode
        }
      }).then((res) => {
        // console.log(res)
        this.NoteList = res;
        // if (res.data.code === 1) {
        //   // this.$message.success("请求成功")
        // } else this.$message.error("请求失败")
      })
    },
    //编辑按钮事件
    updateNote(row) {
      this.dialogUpdateNote = !this.dialogUpdateNote
      this.UpdateNoteList = row;
      console.log(this.UpdateNoteList)
    },
    //确定编辑事件
    updateNote1() {
      this.dialogUpdateNote = !this.dialogUpdateNote,
          this.$http.get("/NoteController/updataNote", {
            params: {
              id: this.UpdateNoteList.id,
              title: this.UpdateNoteList.title,
              statecode:this.UpdateNoteList.statecode
            }
          }).then((res) => {
            console.log(res)
            if (res.data.code === 1) {
              this.$message.success("保存成功")
              this.getNoteList();
            } else this.$message.error("保存失败")
          })
    },
//删除按钮事件
    deleteNote(row) {
      this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$http.get("/NoteController/deleteNote", {
          params: {
            id: row.id,
          }
        }).then((res) => {
          console.log(res)
          if (res.data.code === 1) {
            this.$message.success("删除成功")
            this.getNoteList();
          } else this.$message.error("删除失败")
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    //每页条数发生改变时触发
    SizeChange(limit) {
      this.limit = limit;
      this.getNoteList();
    },
    //页码发生改变时触发
    CurrentChange(page) {
      this.page = page;
      this.getNoteList();
    },
  },
  //  钩子函数
  created() {
    this.getNoteList();
  },
}
</script>

<style scoped>
.a{
  margin-left: 360px;
  margin-top: 20px;
}
</style>
